.list {
  box-sizing: border-box;
  width: 400px;
  padding: 0;
}

.btn {
  height: 30px;
  padding: 0 15px;
  border-radius: 10px;
  border: none;
  background-color: rgb(182, 146, 216);
  color: white;
}

.btn:hover {
  cursor: pointer;
  background-color: blueviolet;
}

.btn:active {
  background-color: rgb(61, 15, 104);
}

.contact {
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
