.form {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  max-width: 300px;
  gap: 10px;
  border: 1px solid black;
  padding: 15px;
  min-width: 400px;
  margin-bottom: 20px;
}

.formBtn {
  width: 100px;
}

.btn {
  margin-right: auto;
  margin-left: auto;
  margin-top: 20px;
  height: 30px;
  width: 150px;
  padding: 0 15px;
  border-radius: 10px;
  border: none;
  background-color: rgb(182, 146, 216);
  color: white;
}

.btn:hover {
  cursor: pointer;
  background-color: blueviolet;
}

.btn:active {
  background-color: rgb(61, 15, 104);
}
